<template>
    <div @touchstart="scrollFixStart" @touchmove="scrollFixMove" class="therapist-list">
        <div v-if="isLoadDb&&therapistList.length==0" class="empty-container">
            <empty-bg :msg="emptyMsg"></empty-bg>
        </div>
        <ul v-else class="therapist-cell-ul">
            <li v-for="item in therapistList" class="therapist-cell-li" :key="item.beautiesid">
                <therapist-cell class="therapist-cell" :cell-data="item" :cell-type="'list'"></therapist-cell>
            </li>
        </ul>
    </div>
</template>

<script>
import { mapState } from "vuex";
import Vue from "vue";
import "mint-ui/lib/toast/style.css";
import therapistCell from "./therapistCell.vue";
import EmptyBg from "../../../popupBox/emptyBg.vue";

export default {
  name: "therapist-list",
  data() {
    return {
      emptyMsg: "快添加美体师吧~",
      curPage: 0,
      isLoadDb: false,
      mockData: [
        {
          headimgurl: "http://www.amyshirley.com/aml/img/headimg/aml_logo.png",
          phone: 13916403296,
          username: "L先生",
          bindMe: true,
          beautiesid: "1"
        }
      ]
    };
  },
  components: {
    EmptyBg,
    therapistCell
  },
  activated() {
  },
  created() {
    //如果已经有美体师的数据
    if(this.therapistList.length>0){
        this.isLoadDb = true;
        return;
    }
    this.$http
      .post(this.path + "php/Route.php?c=Store", {
        type:'showBeautiesList',
        bid:this.userid,
        page:this.curPage
      })
      .then(response => {
        let resultData = response.body.arr;
        this.$store.commit('UPDATA_THERAPIST_LIST',resultData);
        this.isLoadDb = true;
      })
      .catch(response => {
        //请求出错处理
        console.log(response);
      });
  },

  computed: {
    ...mapState(["openid", "path", "userid", "therapistList"])
  },
  methods: {
    scrollFixStart(e) {
      var el = e.currentTarget;
      if (el.scrollTop === 0) {
        el.scrollTop = 1;
      } else if (el.scrollHeight == el.scrollTop + el.offsetHeight) {
        el.scrollTop = el.scrollTop - 1;
      }
    },
    scrollFixMove(e) {
      var el = e.currentTarget;
      if (el.scrollHeight > el.clientHeight) {
        e.stopPropagation();
      }
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.therapist-list {
  width: 100%;
  flex: 1;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  position: relative;

  .therapist-cell-ul {
    width: 100%;

    .therapist-cell-li {
      width: 100%;
      margin-bottom: 5px;
    }
  }

  .empty-container {
    height: 100%;
    width: 100%;
    position: absolute;
  }
}
</style>



